import { NextResponse, type NextRequest } from "next/server";

export function middleware(request: NextRequest) {
  const response = NextResponse.next();
  const geo = (request as NextRequest & { geo?: { country?: string; region?: string } }).geo;

  if (geo?.country === "US" && geo.region) {
    response.headers.set("x-geo-state", geo.region);
  } else {
    response.headers.set("x-geo-state", "");
  }

  return response;
}

export const config = {
  matcher: ["/:path*"],
};
